/*cart item*/

.cart-item {
  display: grid;
  justify-items: center;
  align-items: center;
  grid-auto-flow: column;
  padding: 10px;
  gap: 10px;
}

.cart-item img {
  width: 100%;
  max-width: 128px;
  height: auto;
}

.cart-item .info {
  text-align: start;
}



.cart-item .info * {
  margin: 0px;
}